@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+TC:wght@400;500;700&display=swap');
*, *::before, *::after {
  box-sizing: border-box;
}

html {
  overflow-y: scroll;
}

body {
  margin: 0;
  font-family: 'Noto Sans TC', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Microsoft JhengHei','Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 16px;
  line-height: 1.5; 
  color: #2b2b2b;
}

h1, h2, h3, h4, h5, h6 {
  line-height: 1.2;
  margin: 0;
}

p {
	white-space: pre-line;
  word-break: break-all;
  margin: 0;
}

a {
  text-decoration: none;
  color: inherit;
}

img{
	display: block;
	max-width: 100%;
	height: auto;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

button, input, select {
  outline: none;
  border: none;
  line-height: inherit;
  background: transparent;
}

button {
  cursor: pointer;
}

option {
  min-height: 0;
  padding: 0;
}
